body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  flex-direction: column;
  align-items: center;
  align-content: center;
  
}
@font-face {
  font-family: "Franken";
  src: local("Franken"), url(/Reckless_Racoon_Fan_Web/static/media/franken.9c2a5a0a.otf) format("opentype");
}
@font-face {
  font-family: "Forever";
  src: local("Forever"), url(/Reckless_Racoon_Fan_Web/static/media/forever.34f9dff9.otf) format("opentype");
}
.Faq {
  width:60%

}

.Text-and-image { 
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

}
.Team-list {
  width: 50%;

}
.About-image {
  width:auto;
  border-radius: 5%;
}
.Self-middle {
  flex-direction: row;
}
.Section {
  padding-left: 150px;
  padding-right: 150px;
  padding-bottom: 100px;
  align-content: center;
  align-items: center;
  margin: auto;

}

.Main-image {
  min-height: 100vh;
  width: 100%;
  flex: 1 1;
}

.card-container {
  flex-direction: "row";
  width: 200;
  height: 300;
}

